import React, { useState, useEffect } from 'react';
import Moveable from 'react-moveable';
import LeaderLine from 'leader-line-new';
import './App.css';

const App = () => {
    const [target, setTarget] = useState([]);
    const [start, setStart] = useState(null);
    const [end, setEnd] = useState(null);

    const initialLinks = [];
    const [links, setLinks] = useState(initialLinks);

    const [link_number, setLinkNumber] = useState(0);

    useEffect(() => {
        setTarget([...document.querySelectorAll('.draggable')]);
        console.log(target)
        

        setStart(document.getElementsByClassName("draggable"));
        setEnd(document.getElementsByClassName("scalable"));


    }, []);

    const classNames = [
        'moveable draggable',
        'moveable draggable scalable'
    ]

    function reloadLeader() {
        if (links.length > 0) {
            links.filter(l => {
                l.remove();
            })
        }
        // setStart(document.querySelector("draggable"));
        // setEnd(document.querySelector("scalable"));
        let start = document.getElementById("start");
        let end = document.getElementById("end");

        if (start && end) {
            let link = [];
            link.push(new LeaderLine(start, end, { color: "red", size: 8, startSocket: "right", endSocket: "left" }));
            setLinks(link);
        }
    }
    return (
        <React.Fragment>
            <div><button onClick={reloadLeader}>LOAD</button></div>
            <div className={'moveable draggable'} id='start'>draggable</div>
            <Moveable
                target={target[0]}
                draggable={true}
                origin={false}
                throttleDrag={0}
                onDrag={e => {
                    e.target.style.transform = e.transform;
                }}
            />

            <div className={'moveable draggable scalable'} id='end'>scalable</div>
            <Moveable
                target={target[1]}

                scalable={true}
                throttleScale={0}
                keepRatio={true}
                onScale={e => {
                    e.target.style.transform = e.transform;
                }}
            />
        </React.Fragment>
    );
};

export default App;